import React, { useState, useRef, useEffect } from 'react';
import Fjendulum from './Fjendulum';
import { ReactComponent as HeatmapSVG } from './ElasticPendulChaosHeatmapRender.svg';
import { MathJax, MathJaxContext } from 'better-react-mathjax';

const ElasticPendulumSimulator = () => {
  const [mass, setMass] = useState(0.334);
  const [springConstant, setSpringConstant] = useState(0.087);
  const [cursorPosition, setCursorPosition] = useState({ x: 205, y: 120 });
  const [displayValues, setDisplayValues] = useState({ mass: mass, springConstant: springConstant });
  const svgContainerRef = useRef(null);
  const isDragging = useRef(false);

  const SVG_BOUNDARY = {
    LEFT: 50,
    RIGHT: 298,
    TOP: 78.5,
    BOTTOM: 323
  };

  const kValuesRange = [0.01, 0.1];
  const mValuesRange = [0.1, 0.5];

  useEffect(() => {
    const handleGlobalMouseUp = () => {
      if (isDragging.current) {
        isDragging.current = false;
        document.body.style.overflow = 'auto'; // Allow scrolling again
        const { kValue, mValue } = calculateValues(cursorPosition.x, cursorPosition.y);
        setSpringConstant(kValue);
        setMass(mValue);
      }
    };

    const handleGlobalMouseMove = (event) => {
      if (!isDragging.current || !svgContainerRef.current) return;

      const rect = svgContainerRef.current.getBoundingClientRect();
      let x = event.clientX - rect.left;
      let y = event.clientY - rect.top;

      x = Math.min(Math.max(SVG_BOUNDARY.LEFT, x), SVG_BOUNDARY.RIGHT);
      y = Math.min(Math.max(SVG_BOUNDARY.TOP, y), SVG_BOUNDARY.BOTTOM);
      
      setCursorPosition({ x, y });

      const { kValue, mValue } = calculateValues(x, y);
      setDisplayValues({ mass: mValue.toFixed(3), springConstant: kValue.toFixed(3) });
    };

    const handleTouchMove = (event) => {
      if (!isDragging.current || !svgContainerRef.current) return;

      event.preventDefault(); // Prevent page scrolling

      const rect = svgContainerRef.current.getBoundingClientRect();
      let x = event.touches[0].clientX - rect.left;
      let y = event.touches[0].clientY - rect.top;

      x = Math.min(Math.max(SVG_BOUNDARY.LEFT, x), SVG_BOUNDARY.RIGHT);
      y = Math.min(Math.max(SVG_BOUNDARY.TOP, y), SVG_BOUNDARY.BOTTOM);
      
      setCursorPosition({ x, y });

      const { kValue, mValue } = calculateValues(x, y);
      setDisplayValues({ mass: mValue.toFixed(3), springConstant: kValue.toFixed(3) });
    };

    const handleTouchStart = (event) => {
      isDragging.current = true;
      document.body.style.overflow = 'hidden'; // Prevent scrolling
      event.preventDefault(); // Prevent page scrolling
    };

    const handlePointerDown = (event) => {
      isDragging.current = true;
      document.body.style.overflow = 'hidden'; // Prevent scrolling
      event.preventDefault(); // Prevent page scrolling
    };

    // Add event listeners for the entire SVG container
    const svgContainer = svgContainerRef.current;
    if (svgContainer) {
      svgContainer.addEventListener('mousedown', handlePointerDown);
      svgContainer.addEventListener('touchstart', handleTouchStart);
    }

    document.addEventListener('mouseup', handleGlobalMouseUp);
    document.addEventListener('mousemove', handleGlobalMouseMove);
    document.addEventListener('touchend', handleGlobalMouseUp);
    document.addEventListener('touchmove', handleTouchMove);

    return () => {
      document.removeEventListener('mouseup', handleGlobalMouseUp);
      document.removeEventListener('mousemove', handleGlobalMouseMove);
      document.removeEventListener('touchend', handleGlobalMouseUp);
      document.removeEventListener('touchmove', handleTouchMove);
      if (svgContainer) {
        svgContainer.removeEventListener('mousedown', handlePointerDown);
        svgContainer.removeEventListener('touchstart', handleTouchStart);
      }
    };
  }, [cursorPosition.x, cursorPosition.y]);

  const calculateValues = (x, y) => {
    const normalizedX = (x - SVG_BOUNDARY.LEFT) / (SVG_BOUNDARY.RIGHT - SVG_BOUNDARY.LEFT);
    const normalizedY = (y - SVG_BOUNDARY.TOP) / (SVG_BOUNDARY.BOTTOM - SVG_BOUNDARY.TOP);

    const mValue = mValuesRange[0] + normalizedX * (mValuesRange[1] - mValuesRange[0]);
    const kValue = kValuesRange[0] + (1 - normalizedY) * (kValuesRange[1] - kValuesRange[0]);

    return { kValue, mValue };
  };

  return (
    <MathJaxContext config={{
      loader: { load: ["input/tex", "output/chtml"] },
      options: { enableMenu: false }
    }}>
      <MathJax>
        <div className="w-full flex flex-col md:flex-row overflow-hidden">
          {/* Heatmap with Draggable Cursor */}
          <div className="relative translate-x-6" ref={svgContainerRef}>
            <HeatmapSVG className="w-[400px] h-[400px] max-w-lg" /> {/* Reverted to original SVG size */}

            {/* Draggable cursor */}
            <span
              className="absolute noselect"
              style={{
                left: `${cursorPosition.x}px`,
                top: `${cursorPosition.y}px`,
                transform: 'translate(-50%, -50%)',
                cursor: 'pointer',
                touchAction: 'none', // Prevent touch scrolling
              }}
            >⦿</span>

            {/* Display selected values */}
            <div
              className="absolute border-b border-gray-300 bg-white backdrop-blur-2xl dark:border-white dark:bg-zinc-800/30 text-xs noselect"
              style={{
                left: `${cursorPosition.x}px`,
                top: `${cursorPosition.y}px`,
                transform: 'translate(-50%, -125%)',
              }}>
              <div>Mass: {displayValues.mass} kg</div>
              <div>Spring Constant: {displayValues.springConstant} {'\\(\\frac{N}{\\mathrm{kg}}\\)'}</div>
            </div>
          </div>

          {/* Pendulum Simulation */}
          <div className="flex justify-center" style={{ marginTop: '10px' }}>
            <Fjendulum mass={mass} springConstant={springConstant} />
          </div>
        </div>
      </MathJax>
    </MathJaxContext>
  );
};

export default ElasticPendulumSimulator;
