import React, { useState, useEffect } from 'react';
import Sketch from 'react-p5';

const Pendulum = ({ mass, springConstant }) => {
  const MAX_TRAIL_LENGTH = 100;

  const [angle, setAngle] = useState(Math.PI / 4);
  const [angleVelocity, setAngleVelocity] = useState(-0.5);
  const [angleAcceleration, setAngleAcceleration] = useState(0);
  const [length, setLength] = useState(25);
  const [lengthVelocity, setLengthVelocity] = useState(0);
  const [lengthAcceleration, setLengthAcceleration] = useState(0);
  const [trail, setTrail] = useState([]);

  const restLength = 50;
  const gravity = 10;
  const Damping = 1;

  // Effect to update internal state when mass or springConstant changes
  useEffect(() => {
    // Reset the simulation whenever mass or spring constant changes
    setAngle(Math.PI / 4);
    setAngleVelocity(0);
    setAngleAcceleration(0);
    setLength(25);
    setLengthVelocity(0);
    setLengthAcceleration(0);
    setTrail([]);
  }, [mass, springConstant]);

  const setup = (p5, parentRef) => {
    p5.createCanvas(300, 500).parent(parentRef);
    p5.frameRate(30);
  };

  const draw = (p5) => {
    p5.clear();
    p5.background(255, 0);
    p5.translate(0, 50);

    let newAngleAcceleration = -(gravity / (restLength + length)) * Math.sin(angle) 
      - (2 * lengthVelocity / (restLength + length)) * angleVelocity;
    let newAngleVelocity = angleVelocity + newAngleAcceleration;
    newAngleVelocity *= Damping;
    let newAngle = angle + newAngleVelocity;

    let newLengthAcceleration = (restLength + length) * newAngleVelocity ** 2 
      - (springConstant / mass) * (length - restLength) + gravity * Math.cos(newAngle);
    let newLengthVelocity = lengthVelocity + newLengthAcceleration;
    newLengthVelocity *= Damping;
    let newLength = length + newLengthVelocity;

    const foregroundColor = getComputedStyle(document.documentElement).getPropertyValue('--foreground-rgb');
    const ceilingColor = p5.color(...foregroundColor.split(',').map(Number));
    const trailColor = p5.color(200, 200, 200);

    p5.stroke(ceilingColor);
    p5.strokeWeight(3);
    p5.line(p5.width / 3, 20, p5.width - p5.width / 3, 20);
    
    p5.strokeWeight(1.5);
    for (let i = p5.width / 3; i < p5.width - p5.width / 3; i += 30) {
      p5.line(i, 20, i + 20, 0);
    }

    const x = newLength * Math.sin(newAngle);
    const y = newLength * Math.cos(newAngle);

    const newTrail = [...trail, { x: x + p5.width / 2, y: y + 20, timestamp: p5.frameCount }];

    for (let i = 0; i < newTrail.length - 1; i++) {
      const pos = newTrail[i];
      const alpha = p5.map(p5.frameCount - pos.timestamp, 0, MAX_TRAIL_LENGTH, 255, 0);
      const adjustedAlpha = alpha < 0 ? 0 : alpha;

      p5.stroke(trailColor.levels[0], trailColor.levels[1], trailColor.levels[2], adjustedAlpha);
      p5.strokeWeight(2);
      p5.line(pos.x, pos.y, newTrail[i + 1].x, newTrail[i + 1].y);
    }

    if (newTrail.length > MAX_TRAIL_LENGTH) {
      newTrail.shift();
    }

    p5.stroke(ceilingColor);
    p5.strokeWeight(1.5);
    p5.line(0 + p5.width / 2, 20, x + p5.width / 2, y + 20);
    
    p5.fill(199, 37, 62);
    p5.ellipse(x + p5.width / 2, y + 20, 20, 20);

    setAngle(newAngle);
    setAngleVelocity(newAngleVelocity);
    setAngleAcceleration(newAngleAcceleration);
    setLength(newLength);
    setLengthVelocity(newLengthVelocity);
    setLengthAcceleration(newLengthAcceleration);
    setTrail(newTrail);
  };

  const resetSimulation = () => {
    setAngle(Math.PI / 4);
    setAngleVelocity(0);
    setAngleAcceleration(0);
    setLength(25);
    setLengthVelocity(0);
    setLengthAcceleration(0);
    setTrail([]);
  };

  return (
    <div className="p-2 flex flex-col justify-center items-center">
      <div className="relative w-full">
        <Sketch setup={setup} draw={draw} />
        <button 
          className="absolute top-[45px] right-[55px] text-2xl" 
          onClick={resetSimulation}
          style={{ color: 'var(--foreground-rgb)' }}
        >
          ⟲
        </button>
      </div>
    </div>
  );
};

export default Pendulum;
